import * as React from "react"
import { OutboundLink } from "gatsby-plugin-google-analytics"

const styles = {
  width: "150px",
  height: "60px",
  border: "1px solid #ccc",
  borderRadius: "4px",
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  textDecoration: "none",
  color: "#fd2f3f",
  fontWeight: 500,
}

type StoreButtonProps = {
  text: string
  href: string
  className?: string
}

const StoreButton: React.FC<StoreButtonProps> = ({ text, href, className }) => {
  return (
    <OutboundLink href={href} style={styles} className={className}>
      {text}
    </OutboundLink>
  )
}

export { StoreButton }
