import * as React from "react"

const ParticipantForm = ({ participant }) => {
  const [option1, setOption1] = React.useState(participant.option1)
  const [option2, setOption2] = React.useState(participant.option2)
  const [option3, setOption3] = React.useState(participant.option3)
  const [comment, setComment] = React.useState(participant.comment)
  const [loading, setLoading] = React.useState(false)

  const handleSubmit = (event) => {
    event.preventDefault()

    if (loading) {
      return
    }
    setLoading(true)

    const data = {
      groupId: participant.groupId,
      participantId: participant.id,
      option1: option1,
      option2: option2,
      option3: option3,
      comments: comment,
    }

    fetch(`${process.env.GATSBY_API_HOST}/participant/options/`, {
      method: "PATCH",
      body: JSON.stringify(data),
    })
      .then((response) => response.text())
      .then(() => {
        setLoading(false)
        alert("Informações salvas com sucesso!")
      })
      .catch(() => {
        setLoading(false)
        alert("Algo deu errado. Tente novamente.")
      })
  }

  return (
    <div className="col-12 col-sm-8 col-md-6 col-lg-4 mb-4">
      <form className="card h-100 shadow" onSubmit={handleSubmit}>
        <div className="card-body">
          <h2>{participant.name} quer ganhar:</h2>
          <hr />
          <h6 className="mb-3">Opções de presente</h6>
          <div className="form-group">
            <textarea
              className="form-control"
              placeholder="Opção 1"
              onInput={(e) => setOption1(e.target.value)}
              rows={1}
              defaultValue={participant.option1 ? participant.option1 : ""}
            ></textarea>
          </div>
          <div className="form-group">
            <textarea
              className="form-control"
              placeholder="Opção 2"
              rows={1}
              onInput={(e) => setOption2(e.target.value)}
              defaultValue={participant.option2 ? participant.option2 : ""}
            ></textarea>
          </div>
          <div className="form-group">
            <textarea
              className="form-control"
              placeholder="Opção 3"
              rows={1}
              onInput={(e) => setOption3(e.target.value)}
              defaultValue={participant.option3 ? participant.option3 : ""}
            ></textarea>
          </div>
          <h6 className="mb-3">Observações</h6>
          <div className="form-group">
            <textarea
              className="form-control"
              placeholder="(comentários)"
              rows={5}
              onInput={(e) => setComment(e.target.value)}
              defaultValue={participant.comments ? participant.comments : ""}
            ></textarea>
          </div>
          <button
            type="submit"
            className="gradient-bg border-0 rounded-lg w-100 py-2 text-white"
          >
            {loading ? (
              <span>
                <span
                  className="spinner-border spinner-border-sm mr-1"
                  role="status"
                  aria-hidden="true"
                ></span>
                Salvando...
              </span>
            ) : (
              "Salvar"
            )}
          </button>
        </div>
      </form>
    </div>
  )
}

export { ParticipantForm }
